export default {
  data () {
    return {
      address: wx.getStorageSync('address') || {},
      goodsList: []
    }
  },
  onLoad (options) {
    this.goodsId = options.goodsId
    this.queryGoodsList()
  },
  computed: {
    totalPrice () {
      // 所有被选中商品的数量*价格的和
      return this.goodsList.reduce((sum, v) => {
        return sum + v.num * v.goods_price
      }, 0)
    },
    detailAddr () {
      let { provinceName, cityName, countyName, detailInfo } = this.address
      return provinceName + cityName + countyName + detailInfo
    },
    filterGoodsList () {
      let arr = []
      this.goodsList.forEach(v => {
        arr.push({
          goods_id: v.goods_id,
          goods_number: v.num,
          goods_price: v.goods_price
        })
      })
      return arr
    }
  },
  methods: {
    pay () {
      // 如果用户未选择商品，提示
      if (!this.goodsList.length) {
        wx.showToast({
          title: '请选择要购买的商品',
          icon: 'none'
        })
        return
      }
      // 如果用户未选择地址，提示
      if (!this.address.userName) {
        wx.showToast({
          title: '请填写收货地址',
          icon: 'none'
        })
        return
      }
      this.createOrder()
    },
    // 创建订单
    async createOrder () {
      let data = await this.$request({
        url: '/api/public/v1/my/orders/create',
        isAuth: true,
        method: 'POST',
        data: {
          order_price: this.totalPrice,
          consignee_addr: this.detailAddr,
          goods: this.filterGoodsList
        }
      })
      this.orderNumber = data.order_number
      this.doPay()
      // 创建订单，无论支付成功还是失败，都应该在购物车里面删掉创建订单的商品
      !this.goodsId && this.arrangeCart()
    },
    arrangeCart () {
      this.$store.commit('arrangeCart')
    },
    async doPay () {
      let data = await this.$request({
        url: '/api/public/v1/my/orders/req_unifiedorder',
        isAuth: true,
        method: 'POST',
        data: {
          order_number: this.orderNumber
        }
      })
      wx.requestPayment({
        ...data.pay,
        success: res => {
          wx.navigateTo({ url: '/pages/order_result/main' })
        },
        fail: () => {
          wx.navigateTo({ url: '/pages/order_result/main?orderNumber=' + this.orderNumber })
        }
      })
    },
    async queryGoodsList () {
      let cart = this.$store.getters.getCart
      // 过滤掉未checked的商品
      cart = this.filterCart(cart)
      if (this.goodsId) {
        cart = {
          [this.goodsId]: {
            num: 1,
            checked: true
          }
        }
      }

      let ids = Object.keys(cart).join(',')
      let goodsList = await this.$request({
        url: '/api/public/v1/goods/goodslist?goods_ids=' + ids
      })
      // 合并数据源
      goodsList.forEach(v => {
        v.num = cart[v.goods_id].num
        v.checked = cart[v.goods_id].checked
      })

      this.goodsList = goodsList
    },
    // 过滤掉未checked的商品
    filterCart (cart) {
      let _cart = {...cart}
      for (let key in _cart) {
        if (!_cart[key].checked) {
          delete _cart[key]
        }
      }
      return _cart
    },
    // 从微信同步通讯地址
    getAddr () {
      wx.getSetting({ success: setRes => {
        if (setRes.authSetting['scope.address'] === false) {
          // 用户拒绝
          wx.showModal({
            title: '提示',
            content: '请在设置中允许通讯地址',
            success: res => {
              if (res.confirm) {
                //  打开设置
                wx.openSetting()
              }
            }
          })
        } else {
          // 用户未授权，授权允许
          // 发起授权，调API获取数据
          wx.authorize({
            scope:
                'scope.address',
            success: () => {
              wx.chooseAddress({
                success: (data) => {
                  this.address = data
                  wx.setStorageSync('address', data)
                }
              })
            }
          })
        }
      } })
    }
  }
}
